import React, { Component } from 'react'

class Tile extends Component {
  state = {
    correct: false
  }
  handleChange = (e) => {
    e.preventDefault()
    if (e.target.value.toUpperCase() === this.props.letter) {
      console.log(this.state.correct)
      this.setState({
        correct: true
      })
    } else {
      console.log(this.state.correct)
      this.setState({
        correct: false
      })
    }
  }
  render() {
    if (this.state.correct===true) {
      return (
        <React.Fragment>
            <input className="input" maxLength={1} onChange={this.handleChange} disabled/>
            <span style={{fontSize: '0.6rem'}}>
              {(this.props.number > 0) ? '' : this.props.number}
            </span>
        </React.Fragment>
      )
    } else {
      return (
        <React.Fragment>
            <input maxLength={1} onChange={this.handleChange}/>
            <span style={{fontSize: '0.6rem'}}>
              {(this.props.number > 0) ? '' : this.props.number}
            </span>
        </React.Fragment>
      )
    }
  }
}

export default Tile
